import Vue from 'vue'
import { ToastPlugin, ModalPlugin } from 'bootstrap-vue'
import VueCompositionAPI from '@vue/composition-api'
import flatPickr from "vue-flatpickr-component";
import VueMobileDetection from "vue-mobile-detection";
import axios from 'axios';
import JsonExcel from "vue-json-excel";
import VueApexCharts from 'vue-apexcharts'

import router from './router'
import store from './store'
import App from './App.vue'

// Global Components
import './global-components'

// 3rd party plugins
import '@/libs/portal-vue'
import '@/libs/toastification'
import {
  BCard,
  BCardText,
  BCardTitle,
  BLink,
  BButton,
  BForm,
  BFormGroup,
  BFormInput,
  BFormFile,
  BFormTextarea,
  BFormSelect,
  BFormCheckbox,
  BFormCheckboxGroup,
  BModal,
  VBModal,
  BCardBody,
  BRow,
  BCol,
  BInputGroup,
  BInputGroupAppend,
  BDropdown,
  BDropdownItem,
  BListGroupItem,
  BAvatar,
  BBadge,
  BTable,
  BPagination
} from "bootstrap-vue";

import VueDjGantt from 'vue-dj-gantt'

// import riple
import Ripple from 'vue-ripple-directive'


// BSV Plugin Registration
Vue.use(ToastPlugin)
Vue.use(ModalPlugin)
Vue.use(VueMobileDetection)
Vue.use(VueApexCharts)

// Composition API
Vue.use(VueCompositionAPI)

Vue.prototype.$http = axios;

// import core styles
require('@core/scss/core.scss')

// import assets styles
require('@/assets/scss/style.scss')

Vue.config.productionTip = false

// Ripple directive
Vue.directive('ripple', Ripple)
Vue.directive('b-modal', VBModal)

// Global Components
Vue.component('apexchart', VueApexCharts)
Vue.component('downloadExcel', JsonExcel);
Vue.component('VueDjGantt', VueDjGantt)
Vue.component('flatPickr', flatPickr);
Vue.component('BCardTitle', BCardTitle)
Vue.component('BTable', BTable)
Vue.component('BPagination', BPagination)
Vue.component('BBadge', BBadge)
Vue.component('BListGroupItem', BListGroupItem)
Vue.component('BAvatar', BAvatar)
Vue.component('BCard', BCard)
Vue.component('BCardText', BCardText)
Vue.component('BLink', BLink)
Vue.component('BButton', BButton)
Vue.component('BForm', BForm)
Vue.component('BFormGroup', BFormGroup)
Vue.component('BFormInput', BFormInput)
Vue.component('BFormFile', BFormFile)
Vue.component('BFormTextarea', BFormTextarea)
Vue.component('BFormSelect', BFormSelect)
Vue.component('BFormCheckbox', BFormCheckbox)
Vue.component('BFormCheckboxGroup', BFormCheckboxGroup)
Vue.component('BModal', BModal)
Vue.component('VBModal', VBModal)
Vue.component('BCardBody', BCardBody)
Vue.component('BRow', BRow)
Vue.component('BCol', BCol)
Vue.component('BInputGroup', BInputGroup)
Vue.component('BInputGroupAppend', BInputGroupAppend)
Vue.component('BDropdown', BDropdown)
Vue.component('BDropdownItem', BDropdownItem)

Vue.mixin({
  methods: {
    capitalizeFirstLetter: str => str.charAt(0).toUpperCase() + str.slice(1),
  }
})

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
